import styled from 'styled-components';

export const FeatureImageWrapper = styled.div`
    grid-column: 1 / 14;
    grid-row: 2 / 4;
    overflow: hidden;
    position: relative;
    //border: 1px solid ${props => props.theme.colors.dark3}; 
    border-radius: 10px;

@media ${props => props.theme.breakpoints.tablet} {
    grid-column: 2 /span 6;
}
`